.Projects{
    text-align: center;
    margin-top: 50px;
} 

.Title{
    display: inline-block;
    font-family: 'Roboto', sans-serif; 
    padding: 30px;
    text-align: center;
    color: white;
    background-color: lightgreen;
    border-radius: 25px;
    justify-content: center;
    align-items: center;
    text-shadow: 2px 2px 2px black;
    font-size: 50px;
}

.projectOne{
    display: inline-flex;
    background-color: rgb(182, 142, 170);
    padding: 10px;
    margin: 5px 5px 5px 5px;
    border-radius: 25px;
    justify-content: center;
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: 15px;
    text-shadow: 2px 2px 2px black;
}
.projectTwo{
    display: inline-flex;
    background-color: rgb(182, 142, 170);
    padding: 10px;
    margin: 5px 5px 5px 5px;
    border-radius: 25px;
    justify-content: center;
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: 15px;
    text-shadow: 2px 2px 2px black;
}


@media only screen and (max-width: 500px) {
    .Projects{
        text-align: center;
        margin-top: 30px;
    }

    .Title{
        display: inline-block;
        font-family: 'Roboto', sans-serif;
        padding: 20px;
        text-align: center;
        color: white;
        background-color: lightgreen;
        border-radius: 25px;
        justify-content: center;
        align-items: center;
        text-shadow: 2px 2px 2px black;
        font-size: 40px;
    }
} 