.OnlineTitle{
    display: inline-block;
    align-items: center; 
    font-family: 'Roboto', sans-serif;
    font-size: 90px;
    /* line-height: 70px; */
    text-align: center;
    margin-top: 120px;
    text-shadow: 2px 2px 2px black;
    color: white;
    background-color: lightgreen;
    border-radius: 25px;
    padding: 40px;
}

h2{
    display: inline-block;
    text-shadow: 2px 2px 2px black;
    font-size: 50px; 
    font-family: 'Roboto', sans-serif;
    color: white;
    background-color: rgb(182, 142, 170);
    justify-content: center;
    align-items: center;
    border-radius: 25px;
    margin: 25px;
    padding: 25px;
    
}

a{
    text-decoration: none;
    color: white;
}




@media only screen and (max-width: 500px) {

    .OnlineTitle{
        display: inline-block;
        align-items: center; 
        font-family: 'Roboto', sans-serif;
        font-size: 40px;
        /* line-height: 50px; */
        text-align: center;
        margin-top: 30px;
        
        text-shadow: 2px 2px 2px black;
        color: white;
        background-color: lightgreen;
        border-radius: 25px;
        padding: 20px;
    }


    h2{
        display: inline-block;
        text-shadow: 2px 2px 2px black;
        font-size: 20px; 
        font-family: 'Roboto', sans-serif;
        color: white;
        background-color: rgb(182, 142, 170);
        justify-content: center;
        align-items: center;
        border-radius: 25px;
        margin: 15px;
        padding: 15px;
        margin-top: 25px;
        
    }
    
}