.Home{
    display: flex;
    align-items: center;
    justify-content: center;
}
h1{
    /* display: flex; */
    display: inline-block;
    align-items: center; 
    font-family: 'Roboto', sans-serif;
    font-size: 90px;
    line-height: 90px;
    text-align: center;
    margin-top: 190px;
    text-shadow: 2px 2px 2px black;
    color: white;
    background-color: lightgreen;
    border-radius: 25px;
    padding: 40px;

}



@media only screen and (max-width: 500px) {

    h1{
        /* display: inline-block; */
        display: inline-block;
        font-family: 'Roboto', sans-serif;
        padding: 10px;
        text-align: center;
        color: white;
        background-color: lightgreen;
        border-radius: 25px;
        justify-content: center;
        align-items: center;
        text-shadow: 2px 2px 2px black; 
        margin-top: 50px;
        font-size: 50px;
        line-height: 50px;
    }
}